
#zoom_bar {
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  margin: auto;
  width: 235px;
  height: 30px;
}
#zoom_bar .zoom_bar_btn {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 20px;
    height: 20px;
    margin: auto;
    cursor: pointer;
}
#zoom_bar .zoom_bar_zoomout {
    background: url(../../assets/book/image/zoombar/icon_zoomout.png) no-repeat;
    left: 0;
}
#zoom_bar .zoom_bar_zoomin {
    background: url(../../assets/book/image/zoombar/icon_zoomin.png) no-repeat;
    right: 0;
}
#zoom_bar .zoom_bar_line {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 196px;
    height: 7px;
    margin: auto;
    background: url(../../assets/book/image/zoombar/icon_bar.png) no-repeat;
    cursor: pointer;
}
#zoom_bar .zoom_bar_auto {
    background: url(../../assets/book/image/zoombar/icon_auto.png) no-repeat;
    background-position: center;
    left: -25px;
}
#zoom_bar .zoom_bar_pointer {
    background: url(../../assets/book/image/zoombar/icon_pointer.png) no-repeat;
    width: 16px;
    height: 26px;
}



