
#edit_text_pannel {
  position: absolute;
  z-index: 1005;
  -webkit-transform-origin: center top;
          transform-origin: center top;
}
#edit_text_pannel .content {
    position: relative;
    background: rgba(75, 75, 75, 0.8);
    -webkit-box-shadow: 0 1px 6px #999;
            box-shadow: 0 1px 6px #999;
    border-radius: 5px;
    padding: 20px;
    text-align: left;
    overflow: hidden;
}
#edit_text_pannel .content .edit_input {
      resize: none;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      border: none;
      margin-bottom: 10px;
      padding: 5px;
      font-size: 12px;
      overflow-y: scroll;
      outline: none;
}
#edit_text_pannel .content p {
      font-size: 12px;
      margin: 0;
      color: white;
}
#edit_text_pannel .content p.edit_text_notic1 {
        margin-bottom: 5px;
        font-weight: bold;
}
#edit_text_pannel .content p.edit_text_notic2 {
        margin-bottom: -5px;
        color: #f4803a;
}
#edit_text_pannel .content .edit_text_btn {
      cursor: pointer;
      position: absolute;
      font-size: 12px;
      right: 20px;
      bottom: 9px;
      display: inline-block;
      height: 28px;
      line-height: 28px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      background: #d2523e;
      border-radius: 5px;
      color: white;
      padding: 0 10px 0 10px;
}
#edit_text_pannel .content .edit_text_btn:hover {
        opacity: .9;
}
#edit_text_pannel .content .edit_text_btn:active {
        opacity: .8;
}
#edit_text_pannel .edit_text_pannel_arrow {
    position: absolute;
    width: 0;
    height: 0;
    margin: auto;
}
#edit_text_pannel .textbox_preview {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    white-space: pre-wrap;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}
#edit_text_pannel .textbox_preview .textbox_preview_content {
      position: absolute;
      white-space: pre-wrap;
      -ms-word-wrap: break-word;
      word-wrap: break-word;
}



