
.edit-definition_warning_content {
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  font-size: 12px;
  padding: 7px;
  color: black;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 8px #999;
  box-shadow: 0 1px 8px #999;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 2000;
}
.edit-definition_warning_content h3 {
    margin-bottom: 2px;
    display: inline-block;
    font-size: 16px;
}
.edit-definition_warning_content p.advice_size {
    display: inline;
}
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



