
#edit {
  position: absolute;
  top: 105px;
  left: 215px;
  right: 166px;
  bottom: 127px;
  z-index: 1000;
  overflow: hidden;
  -webkit-box-shadow: inset 0 1px 8px #aaa;
  box-shadow: inset 0 1px 8px #aaa;
}
#edit.--preview {
  top: 0;
  left: 0;
  right: 0;
}
#eidt_pannel {
  position: absolute;
  background: #fff;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-box-shadow: 0 1px 10px #999;
  box-shadow: 0 1px 10px #999;
}
#eidt_pannel[data-num=front_cover]:before {
    content: '';
    position: absolute;
    top: -4%;
    left: -3%;
    right: 3%;
    bottom: 100%;
    background-image: url(./../../assets/book/image/spine/top_left.png), url(./../../assets/book/image/spine/top_right.png), url(./../../assets/book/image/spine/top_repeat.png);
    background-position: left top, right top, left top;
    background-repeat: no-repeat, no-repeat, repeat-x;
    background-size: 4% 100%, contain, contain;
}
#eidt_pannel[data-num=front_cover]:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -3%;
    right: 100%;
    background-image: url(./../../assets/book/image/spine/left_bottom.png), url(./../../assets/book/image/spine/left_repeat.png);
    background-position: left bottom, left top;
    background-repeat: no-repeat, repeat-y;
    background-size: contain;
}
#eidt_pannel[data-num=front_cover][data-nocover=true] {
    background: none;
    -webkit-box-shadow: none;
            box-shadow: none;
}
#eidt_pannel[data-num=front_cover][data-nocover=true]:before, #eidt_pannel[data-num=front_cover][data-specialcover=true]:before {
    content: none;
}
#eidt_pannel[data-num=front_cover][data-nocover=true]:after, #eidt_pannel[data-num=front_cover][data-specialcover=true]:after {
    content: none;
}
#eidt_pannel[data-num=spine]:after {
    content: '';
    position: absolute;
    top: -6%;
    bottom: -7%;
    left: -60%;
    right: -600%;
    background-image: url(./../../assets/book/image/spine/spine_bg.png);
    background-position: left top;
    background-repeat: no-repeat;
    background-size: contain;
}
#eidt_pannel .shadow_line {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0px;
    right: 0;
    margin: auto;
    -webkit-box-shadow: 0 0 18px 1px black;
    box-shadow: 0 0 18px 1px black;
    z-index: 1000;
    width: 1px;
    background: #999;
}
#preview-button_return_edit {
  position: absolute;
  bottom: 22px;
  left: 0;
  height: 38px;
  width: 105px;
  font-size: 14px;
  border: none;
  border-bottom: 4px solid #ac4030;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  line-height: 1em;
}
#preview-button_return_edit:active {
    border-bottom: 0;
    opacity: 0.8;
}
#preview-button_return_edit:hover {
    border-bottom: 3px solid #ac4030;
}
#preview-button_return_edit:before {
    content: '\e6d4';
    font-family: iconfont;
    font-size: 22px;
    vertical-align: middle;
    position: relative;
    left: -3px;
}
.fadeIn-enter-active, .fadeIn-leave-active {
  -webkit-transition: all .6s;
  transition: all .6s;
}
.fadeIn-enter, .fadeIn-leave-to {
  opacity: 0;
}
.fadeIn-enter-to, .fadeIn-leave {
  opacity: 1;
}



