
#drag_size_tip {
  position: absolute;
  z-index: 2000;
  top: 0;
  left: 0;
  background: #5587ce;
  color: white;
  padding: 3px;
  font-size: 12px;
  border-radius: 4px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}



