
.textbox {
  overflow: hidden;
}
.textbox.no_content, .textbox:hover, .textbox.active {
    background: rgba(200, 200, 200, 0.3);
    -webkit-box-shadow: inset 1px 0 10px #ccc;
            box-shadow: inset 1px 0 10px #ccc;
    cursor: pointer;
}
.textbox:hover {
    background: rgba(230, 230, 230, 0.3);
    -webkit-box-shadow: inset 1px 0 10px #ccc;
            box-shadow: inset 1px 0 10px #ccc;
}
.textbox .content {
    white-space: pre-wrap;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    overflow: hidden;
    width: 100%;
    height: 100%;
}
.textbox .content:after {
      content: attr(txt);
}
.textbox.edit:hover {
    cursor: auto;
}
.ispreview .no_content {
  display: none;
}



