/* 清空默认样式 */
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strong, sub, sup, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video, input {
  margin: 0;
  padding: 0;
}

ol, ul {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input, button, textarea {
  outline: none;
}
/* 清空默认样式 end */

/* mini滚动条 */
.mini-scroll::-webkit-scrollbar {
  width:10px;
  height:10px;
}
.mini-scroll::-webkit-scrollbar-button{
  display: none;
}
.mini-scroll::-webkit-scrollbar-track{
  background:#fff;
}
.mini-scroll::-webkit-scrollbar-track-piece{
  background: #F1F1F1;
}
.mini-scroll::-webkit-scrollbar-thumb{
  background:#e3e3e3;
  border-radius:10px;
}
.mini-scroll::-webkit-scrollbar-corner{
  background:#82AFFF;
}
.mini-scroll::-webkit-scrollbar-resizer{
  background:#FF0BEE;
}
/* mini滚动条 end */

.jumpIn-enter-active,.jumpIn-leave-active{
  transition-property: transform, opacity;
  transition-duration: .2s;
}
.jumpIn-enter,.jumpIn-leave-to{
  transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
  opacity: 0;
}
.jumpIn-enter-to,.jumpIn-leave{
  opacity: 1;
  transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
}

.addFadeIn-enter-active,.addFadeIn-leave-active{
  transition-property: top, opacity;
  transition-duration: .2s;
  transition-timing-function: linear;
}
.addFadeIn-enter,.addFadeIn-leave-to{
  top: -10px;
  opacity: 0;
}
.addFadeIn-enter-to,.addFadeIn-leave{
  opacity: 1;
  top: 0;
}


