
.select-list_wrapper {
  position: absolute;
  z-index: 201;
  background: #ededed;
  -webkit-box-shadow: 0 1px 4px #999;
  box-shadow: 0 1px 4px #999;
  margin-left: 1px;
  max-height: 120px;
  -webkit-transform-origin: center top;
          transform-origin: center top;
  overflow: hidden;
  overflow-y: scroll;
}
.select-list_wrapper ul li {
    display: block;
    text-align: center;
    height: 24px;
    padding: 5px;
    font-size: 12px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #474747;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.select-list_wrapper ul li:hover, .select-list_wrapper ul li.active {
      background: #89cef4;
}



