
.right-btn-menu {
  position: absolute;
  z-index: 2002;
  border: 1px solid rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  padding: 3px 0;
  background-color: white;
}
.right-btn-menu .menubtn {
    padding: 6px 10px;
    display: block;
    color: #333;
    cursor: pointer;
}
.right-btn-menu .menubtn:hover {
      background-color: #eee;
}



