
#save_tip {
  position: absolute;
  top: 130px;
  left: 0;
  right: 0;
  width: 130px;
  height: 58px;
  line-height: 58px;
  margin: auto;
  padding-left: 60px;
  background: url(../../assets/book/image/icon_savetip_bg.png) no-repeat;
  font-size: 26px;
  -webkit-text-shadow: 0 0 2px #aaa;
  text-shadow: 0 0 2px #aaa;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  z-index: 2001;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
#save_tip[data-state="0"] {
    visibility: hidden;
    -webkit-transition: none;
    transition: none;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
}
#save_tip[data-state="1"] {
    visibility: visible;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
}
#save_tip[data-state="2"] {
    visibility: hidden;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0;
}



