
.page {
  position: relative;
  z-index: 1;
  display: inline-block;
  overflow: hidden;
}
.page .locator {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    cursor: -webkit-grab;
}
.page .box {
    position: absolute;
    border: 0 solid white;
}
.page[data-num='front_flap'] {
    -webkit-box-shadow: 0 1px 10px #aaa;
            box-shadow: 0 1px 10px #aaa;
}
.page.animation {
    -webkit-transition: all .2s;
    transition: all .2s;
}
.page.animation .box {
      -webkit-transition: all .2s;
      transition: all .2s;
}
.page.animation .box .img_cover {
        -webkit-transition: all .2s;
        transition: all .2s;
}
.page.animation .box img {
        -webkit-transition: all .2s;
        transition: all .2s;
}
.page.animation .box .content {
        -webkit-transition: all .2s;
        transition: all .2s;
}



