html{
  overflow: hidden;
}

.section_title{
  background: url('../image/title_bg.png') repeat-x;
  background-size: contain;
  height: 28px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 15px;
  font-weight: normal;
  letter-spacing: 1px;
  -webkit-text-shadow: 0 0 1px rgba(255, 255, 255, 0.7);
  text-shadow: 0 0 1px rgba(255, 255, 255, 0.7);
  color: #fff;
}

/* 对话弹窗 */
.dialog {
  position: absolute;
  color: black;
  font-size: 14px;
  display: none;
  -webkit-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 2000;
  padding: 10px;
  -webkit-box-shadow: 0 1px 6px #999;
  box-shadow: 0 1px 6px #999;
  border-radius: 5px;
  background: white;
}

.dialog-show{
  backface-visibility: initial;
  display: block;
  visibility: visible;
  animation: fadein-right 0.6s;
}

.dialog-button {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background: #d2523e;
  border-radius: 5px;
  color: white;
  padding: 0 10px 0 10px;
  text-align: center;
  cursor: pointer;
}

.dialog-button-close {
  position: absolute;
  top: 0;
  right: 0;
  height: 25px;
  width: 25px;
  font-family: SimHei;
  border-bottom-left-radius: 25px;
  border-top-right-radius: 5px;
  cursor: pointer;
  background: #ddd;
}

.dialog-button-close > .text {
  position: absolute;
  top: 2px;
  right: 3px;
  color: #6088ce;
  font-weight: bold;
  font-size: 14px;
  pointer-events: none;
}
/* 对话弹窗 end */





