
@-webkit-keyframes focus {
0% {
    opacity: 1;
}
25% {
    opacity: 0;
}
50% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes focus {
0% {
    opacity: 1;
}
25% {
    opacity: 0;
}
50% {
    opacity: 1;
}
75% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.focus {
  -webkit-animation: focus 1s;
          animation: focus 1s;
}
.imgbox {
  overflow: hidden;
}
.imgbox .img_content {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    left: 0;
    top: 0;
}
.imgbox .img_content img {
      position: absolute;
}
.imgbox .img_cover {
    position: absolute;
    left: 0;
    background: #000;
    visibility: visible;
    opacity: .8;
}
.imgbox .img_cover img {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: .3;
}
.imgbox .require_size {
    position: absolute;
    color: white;
    top: 10px;
    left: 10px;
    font-size: 20px;
}
.imgbox .no_image {
    width: 100%;
    height: 100%;
    background: #ccc;
}
.imgbox .no_image:after {
      content: attr(text);
      position: absolute;
      font-size: 14px;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      height: 20px;
      text-align: center;
}
.imgbox:hover, .imgbox:active {
    background: rgba(230, 230, 230, 0.3);
    -webkit-box-shadow: inset 1px 0 10px #ccc;
            box-shadow: inset 1px 0 10px #ccc;
    cursor: pointer;
}
.imgbox.edit {
    background: #ccc;
    overflow: visible;
}
.imgbox.edit:hover {
      cursor: all-scroll;
}
.imgbox.drag-in .no_image {
    background-color: #cdc;
}
.addImg-enter-active, .addImg-leave-active {
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: .2s;
          transition-duration: .2s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}
.addImg-enter, .addImg-leave-to {
  -webkit-transform: scale(1.7);
          transform: scale(1.7);
  opacity: 0;
}
.addImg-enter-to, .addImg-leave {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.definition_warning {
  position: absolute;
  top: 20px;
  right: 20px;
  margin: auto;
  background: red;
  font-family: SimHei;
  font-size: 26px;
  line-height: 36px;
  height: 36px;
  width: 36px;
  font-weight: bold;
  text-align: center;
  color: white;
  -webkit-box-shadow: 0 1px 8px #999;
  box-shadow: 0 1px 8px #999;
  border-radius: 4px;
  z-index: 2000;
  cursor: pointer;
  -webkit-transform-origin: right top;
  transform-origin: right top;
}
.ispreview .no_image {
  display: none;
}
.ispreview .require_size {
  display: none;
}



