
@charset "UTF-8";
.mini_header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1005;
  width: 100%;
  background-color: #4d1e24;
  height: 40px;
  -webkit-box-shadow: 0 0 2px black;
          box-shadow: 0 0 2px black;
  position: relative;
}
.mini_header .wrapper {
    margin: 0 auto;
    position: relative;
}
.mini_header .wrapper .logo {
      position: absolute;
      top: 5px;
      left: 20px;
}
.mini_header .wrapper .nav {
      position: absolute;
      right: 20px;
      font-family: 'Microsoft YaHei';
}
.mini_header .wrapper .nav span, .mini_header .wrapper .nav a {
        display: block;
        float: left;
        height: 40px;
        line-height: 40px;
}
.mini_header .wrapper .nav span {
        color: #8e6659;
}
.mini_header .wrapper .nav a {
        color: #dec7bf;
        padding: 0 10px;
        text-decoration: none;
        cursor: pointer;
}
.mini_header .wrapper .nav a:hover {
          background: #683239;
}
.mini_header .wrapper #bookname {
      position: absolute;
      color: #dec7bf;
      top: 0;
      left: 180px;
      z-index: 1;
      font-size: 15px;
      height: 40px;
      line-height: 40px;
      cursor: pointer;
}
.mini_header .wrapper #bookname:before {
        content: '《';
        vertical-align: top;
}
.mini_header .wrapper #bookname:after {
        content: '》';
        vertical-align: top;
}
.mini_header .wrapper #bookname:hover {
        background: #683239;
}
#lang {
  position: absolute;
  top: 40px;
  right: 20px;
  background: #8e5e64;
  border-radius: 0 0 3px 3px;
}
#lang > ul > li {
    width: 80px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    color: #dec7bf;
    cursor: pointer;
}
#lang > ul > li:hover {
      color: #dec7bf;
      background: #683239;
}
#lang > ul > li:last-child:hover {
      border-radius: 0 0 3px 3px;
}



