
.resize-wrap {
  z-index: 1;
  position: relative;
  top: 0;
  left: 0;
}
.resize-wrap .selection-border {
  position: absolute;
  z-index: 1000;
  width: 2px;
  height: 2px;
  background: #5587ce;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}
.resize-wrap .selection-handle {
  position: absolute;
  z-index: 1001;
  background: #fff;
  border: 2px solid #5587ce;
  cursor: pointe;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}
.resize-wrap .border {
  position: absolute;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 2px;
  height: 2px;
  background: #5587ce;
  -webkit-box-shadow: 0 0 4px #aaa;
          box-shadow: 0 0 4px #aaa;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}
.resize-wrap .border .rotatelink {
  position: absolute;
  z-index: 1000;
  height: 12px;
}
.resize-wrap .selection-handle.top-left {
  cursor: se-resize;
}
.resize-wrap .selection-handle.top-right {
  cursor: sw-resize;
}
.resize-wrap .selection-handle.bottom-center {
  cursor: s-resize;
  z-index: 1002;
}
.resize-wrap .selection-handle.center-right {
  cursor: e-resize;
  z-index: 1003;
}
.resize-wrap .selection-handle.top-center {
  cursor: n-resize;
  z-index: 1002;
}
.resize-wrap .selection-handle.bottom-right {
  cursor: nw-resize;
}
.resize-wrap .selection-handle.center-left {
  cursor: e-resize;
  z-index: 1003;
}
.resize-wrap .selection-handle.bottom-left {
  cursor: ne-resize;
}
.resize-wrap .selection-handle.rotate {
  cursor: -webkit-grab;
  border-radius: 100%;
  background: #60a0e0;
  border: none;
}



