.toolbar-select {
  position: absolute;
  background: #ededed;
  z-index: 1000;
  -webkit-box-shadow: 0 1px 6px #666;
          box-shadow: 0 1px 6px #666;
  top: 0;
  left: 0;
  -webkit-transform-origin: center top;
          transform-origin: center top;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.toolbar-select:before {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  background: inherit;
  -webkit-box-shadow: 0 1px 6px #666;
          box-shadow: 0 1px 6px #666;
  -webkit-transform: scaleX(0.8) rotate(45deg);
          transform: scaleX(0.8) rotate(45deg);
  top: -4px;
  left: 0;
  right: 0;
  margin: auto;
}
.toolbar-select-shading {
  margin-left: -270px;
  background: #FFF;
}
.toolbar-select-shading:before {
  margin-left: 35px;
}
.toolbar-select-body {
  background: inherit;
