
@-webkit-keyframes fadeout {
80% {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0);
}
100% {
    opacity: 0;
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
}
}
@keyframes fadeout {
80% {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0);
}
100% {
    opacity: 0;
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
}
}
#bleed {
  pointer-events: none;
}
#bleed.active .bleed_line {
    background: #fcc;
}
#bleed .bleed_checked {
    position: absolute;
    opacity: 0.3;
    z-index: 1002;
    background: #f00;
}
#bleed .bleed_line {
    position: absolute;
    opacity: 0.8;
    z-index: 1001;
    background: #eee;
    -webkit-box-shadow: 0 0 2px #888;
            box-shadow: 0 0 2px #888;
}
#bleed .bleed_left {
    top: 0;
    bottom: 0;
    left: 0;
}
#bleed .bleed_right {
    top: 0;
    bottom: 0;
    right: 0;
}
#bleed .bleed_top {
    left: 0;
    right: 0;
    top: 0;
}
#bleed .bleed_bottom {
    left: 0;
    right: 0;
    bottom: 0;
}
#bleed .bleed_tip {
    font-size: 18px;
    position: absolute;
    background-color: #fec;
    padding: 13px 25px 13px 25px;
    border-radius: 5px;
    border: 1px solid #aa0;
    z-index: 1020;
    color: #666;
    -webkit-animation: fadeout 6s;
            animation: fadeout 6s;
}
#bleed .bleed_tip.tip_left {
      top: 100px;
      left: 6%;
}
#bleed .bleed_tip.tip_right {
      top: 100px;
      right: 6%;
}
#bleed .bleed_tip.tip_top {
      left: 200px;
      top: 6%;
}
#bleed .bleed_tip.tip_bottom {
      left: 200px;
      bottom: 6%;
}
#bleed .bleed_tip:before {
      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      background: inherit;
      -webkit-transform: scaleX(1) rotate(45deg);
      transform: scaleX(1) rotate(45deg);
}
#bleed .bleed_tip.tip_left:before {
      left: -5px;
      border-left: 1px solid #aa0;
      border-bottom: 1px solid #aa0;
}
#bleed .bleed_tip.tip_right:before {
      right: -5px;
      border-right: 1px solid #aa0;
      border-top: 1px solid #aa0;
}
#bleed .bleed_tip.tip_top:before {
      left: 20px;
      top: -5px;
      border-left: 1px solid #aa0;
      border-top: 1px solid #aa0;
}
#bleed .bleed_tip.tip_bottom:before {
      left: 20px;
      bottom: -5px;
      border-right: 1px solid #aa0;
      border-bottom: 1px solid #aa0;
}



