.edit-button-bordercolor .color {
  position: absolute;
  left: 0;
  right: 4px;
  left: 4px;
  bottom: 2px;
  height: 2px;
}
.edit-button-bordercolor:after {
  width: 14px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/boxtool-bordercolor.png);
}
.edit-button-bgcolor .color {
  position: absolute;
  background: #2db572;
  height: 12px;
  width: 10px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.edit-button-bgcolor .color:before {
    content: ' ';
    display: block;
    position: absolute;
    background: inherit;
    left: -5px;
    top: 5px;
    width: 10px;
    height: 2px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.edit-button-bgcolor .color:after {
    content: ' ';
    display: block;
    position: absolute;
    background: inherit;
    left: 8px;
    top: 2px;
    width: 6px;
    height: 3px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
