#tooltip {
  position: absolute;
  padding: 3px;
  top: 29px;
  border: 1px solid #ccc;
  background: white;
  font-size: 12px;
  color: #999;
  z-index: 2001;
  -webkit-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}
.fadeIn-enter-active,
.fadeIn-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.fadeIn-enter,
.fadeIn-leave-to {
  opacity: 0;
}
.fadeIn-enter-to,
.fadeIn-leave {
  opacity: 1;
}
.click_lock {
  background: #904747;
  color: white;
  width: 75px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 3px;
  position: absolute;
  left: 40px;
  top: 0;
  font-size: 12px;
}
.click_lock .arrows {
    border-width: 8px;
    border-style: solid;
    border-color: transparent #904747 transparent transparent;
    position: absolute;
    left: -14px;
    top: 7px;
}
.blink {
  -webkit-animation: blink .2s 3;
          animation: blink .2s 3;
}
@-webkit-keyframes blink {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes blink {
0% {
    opacity: 1;
}
50% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



