
#book_paramet {
  position: absolute;
  width: 905px;
  height: 530px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2001;
  margin: auto;
  border: solid 1px #bbb;
  border-radius: 3px;
  background: #f8f8f8;
  -webkit-box-shadow: 1px 1px 5px #909090;
          box-shadow: 1px 1px 5px #909090;
}
#book_paramet .title {
    height: 40px;
    line-height: 40px;
    color: #fff;
    padding-left: 20px;
    font-size: 15px;
    border-radius: 3px 3px 0 0;
}
#book_paramet .paramet_vessel {
    width: 843px;
    height: 380px;
    border: solid 1px #f0f0f0;
    background: #fff;
    margin: 30px 30px 20px;
}
#book_paramet .paramet_vessel .paramet_item {
      display: inline-block;
      vertical-align: top;
      width: 613px;
      border-right: dashed 1px #ccc;
      margin: 15px 0;
}
#book_paramet .paramet_vessel .paramet_item > ul > li {
        margin: 0 30px;
        padding: 12px 0;
        border-bottom: solid 1px #ddd;
}
#book_paramet .paramet_vessel .paramet_item > ul > li:last-child {
          border: none;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_title {
          display: inline-block;
          vertical-align: middle;
          width: 20px;
          color: #943539;
          font-size: 14px;
          font-weight: bold;
          padding: 5px;
          text-align: center;
          line-height: 20px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel {
          position: relative;
          display: inline-block;
          vertical-align: middle;
          margin-left: 25px;
          width: 490px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel > ul > li {
            display: inline-block;
            vertical-align: middle;
            margin-right: 10px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel > ul > li .item_binding {
              width: 148px;
              height: 38px;
              line-height: 38px;
              border: solid 2px #f0f3f6;
              background: #f0f3f6;
              color: #555555;
              padding-top: 2px;
              margin-top: 5px;
              text-align: center;
              cursor: pointer;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel > ul > li .item_binding.active {
                border: solid 2px #5488cd;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel > ul > li .item_paper {
              width: 100px;
              height: 32px;
              line-height: 32px;
              background: #f0f3f6;
              border: solid 2px #f0f3f6;
              text-align: center;
              margin-top: 5px;
              cursor: pointer;
              position: relative;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel > ul > li .item_paper.active {
                border: solid 2px #5488cd;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar {
            width: 106px;
            height: 34px;
            line-height: 34px;
            text-align: left;
            padding-left: 7px;
            color: #555;
            background: #fcfcfc;
            border: solid 1px #ddd;
            border-radius: 3px;
            font-size: 12px;
            cursor: pointer;
            position: relative;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar:before {
              content: "";
              width: 1px;
              height: 34px;
              background: #ddd;
              float: right;
              margin-right: 16px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar:after {
              content: "";
              border-width: 5px;
              border-style: solid;
              border-color: #888 transparent transparent transparent;
              position: absolute;
              right: 3px;
              top: 14px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar_select {
            width: 113px;
            border: 1px solid #ddd;
            position: absolute;
            top: 34px;
            height: 112px;
            overflow-y: scroll;
            background: #fff;
            z-index: 1;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar_select .item_pageCount {
              line-height: 20px;
              padding-left: 7px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar_select .item_pageCount:hover {
                background: #ddd;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .item_pageBar_select .item_pageCount.active {
                border: solid 2px #5488cd;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel.item_vessel_paper {
            height: 82px;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .paper_tip {
            width: 400px;
            line-height: 18px;
            padding: 8px;
            margin-top: 8px;
            border: dashed 1px #ede8d0;
            background: #fffff8;
            position: absolute;
            z-index: 1;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .paper_tip:before {
              content: '';
              position: absolute;
              left: 7px;
              top: -12px;
              width: 0px;
              height: 0px;
              border: dashed 6px transparent;
              border-bottom: dashed 6px #DADA25;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel .paper_tip:after {
              content: '';
              position: absolute;
              left: 7px;
              top: -11px;
              width: 0px;
              height: 0px;
              border: dashed 6px transparent;
              border-bottom: dashed 6px #fff;
}
#book_paramet .paramet_vessel .paramet_item > ul > li .item_vessel > p {
            line-height: 20px;
}
#book_paramet .paramet_vessel .paramet_price {
      display: inline-block;
      vertical-align: top;
      width: 165px;
      padding: 30px;
}
#book_paramet .paramet_vessel .paramet_price .price_title {
        color: #943539;
        font-size: 14px;
        font-weight: bold;
}
#book_paramet .paramet_vessel .paramet_price .price_vessel > p {
        margin-top: 10px;
        font-size: 14px;
}
#book_paramet .paramet_vessel .paramet_price .price_vessel > p span {
          color: #fd8243;
          font-weight: bold;
}
#book_paramet .paramet_btn_list {
    margin: 0 30px;
}
#book_paramet .paramet_btn_list .paramet_btn {
      width: 120px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      color: #fff;
      border: solid 1px #f3671d;
      border-radius: 3px;
      background: #fe8444;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
}
#book_paramet .paramet_btn_list .paramet_btn.cancel {
        float: left;
}
#book_paramet .paramet_btn_list .paramet_btn.confirm {
        float: right;
}



