
.decorabox .content {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
}
.decorabox .content img {
    width: 100%;
    height: 100%;
}
.decorabox:hover {
  cursor: pointer;
}
.decorabox .mycanvas {
  opacity: 0;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.decorabox .decora_text {
  position: absolute;
  white-space: pre-wrap;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  overflow: hidden;
}
.decorabox .decora_text .text_content:after {
    content: attr(txt);
}
.decorabox .decora_text:hover, .decorabox .decora_text.active {
    background: rgba(200, 200, 200, 0.3);
    -webkit-box-shadow: inset 1px 0 10px #ccc;
            box-shadow: inset 1px 0 10px #ccc;
    cursor: pointer;
}
.decorabox .decora_text:hover {
    background: rgba(230, 230, 230, 0.3);
    -webkit-box-shadow: inset 1px 0 10px #ccc;
            box-shadow: inset 1px 0 10px #ccc;
}



