/* 文字图标 */
@font-face {
  font-family: "iconfont";
  src: url('../../fontfiles/iconfont.eot'); /* IE9*/
  src: url('../../fontfiles/iconfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../../fontfiles/iconfont.woff') format('woff'), /* chrome、firefox */
  url('../../fontfiles/iconfont.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url('../../fontfiles/iconfont.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}



