
#edit-props {
  background: #ededed;
  width: 280px;
  z-index: 1005;
}
#edit-props .toolbar-select-topbar {
    height: 24px;
    background: #ddd;
    background: -webkit-gradient(linear, left top, left bottom, from(#ddd), to(#aaa));
    background: linear-gradient(to bottom, #ddd 0%, #aaa 100%);
    -webkit-box-shadow: 0 1px 6px #999;
    box-shadow: 0 1px 6px #999;
}
#edit-props .toolbar-select-topbar[data-drag="true"] {
      cursor: -webkit-grab;
}
#edit-props .toolbar-select-topbar .topbar_nail {
      background: url(../../assets/book/image/boxtoolbar/boxtool-nail.png);
      background-size: 100%;
      background-position: 0px 2px;
      width: 20px;
      height: 20px;
      margin-left: 10px;
      cursor: pointer;
}
#edit-props .props_list {
    display: inline-block;
    vertical-align: middle;
    margin: 5px;
}
#edit-props .props_list .empty {
      width: 15px;
      height: 15px;
      border: solid 1px #ededed;
}
#edit-props .props_list .lock {
      position: relative;
      background: url(../../assets/book/image/boxtoolbar/boxtool-sizelock.png);
      background-size: 100%;
      cursor: pointer;
}
#edit-props .props_list .lock:hover {
        border: solid 1px #aaa;
}
#edit-props .props_list .lock[data-lock="true"], #edit-props .props_list .lock[data-fixlock="true"] {
        border: solid 1px #aaa;
        -webkit-box-shadow: 1px 1px 3px #aaa inset;
                box-shadow: 1px 1px 3px #aaa inset;
}
#edit-props .props_list .lock[data-fixlock="true"]:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #aaa;
        opacity: .8;
        cursor: no-drop;
}
#edit-props:before {
    content: none;
}
#edit-props .btn_tip {
    position: absolute;
    left: 0;
    top: 0;
    padding: 3px;
    border: 1px solid #ccc;
    background: white;
    font-size: 12px;
    color: #999;
    z-index: 2001;
    -webkit-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}
.input_name {
  display: inline-block;
  width: 15px;
  text-align: right;
}
.input_box {
  width: 50px;
  height: 24px;
  line-height: 24px;
  padding-left: 5px;
  border: solid 1px #ddd;
  font-size: 12px;
  -webkit-box-shadow: 1px 1px 3px #ddd inset;
          box-shadow: 1px 1px 3px #ddd inset;
}



