
#pagesort {
  position: absolute;
  top: 105px;
  left: 215px;
  right: 166px;
  bottom: 127px;
}
#pagesort .notice {
    color: black;
    font-size: 14px;
    line-height: 14px;
    -webkit-text-shadow: 0 0 1px #aaa;
    text-shadow: 0 0 1px #aaa;
    padding: 20px;
    float: left;
}
#pagesort .notice input {
      vertical-align: top;
      line-height: 1;
      margin-right: 2px;
}
#pagesort #pagesort-buttonarea {
    position: absolute;
    top: 10px;
    right: 20px;
}
#pagesort #pagesort-buttonarea button {
      height: 28px;
      /* width: 70px; */
      float: left;
      margin-left: 8px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background: #d2523e;
      border: none;
      border-bottom: 2px solid #ac4030;
      outline: none;
      border-radius: 5px;
      color: white;
      padding: 0 10px 0 10px;
}
#pagesort #pagesort-buttonarea button.btn_color_01 {
        background: #B3B0B0;
        border-bottom: 2px solid #939090;
}
#pagesort #pagesort-buttonarea button:active {
        opacity: .7;
        border-bottom: none;
}
#pagesort #pagesort-page_list_wrapper {
    position: absolute;
    overflow-y: scroll;
    overflow-x: hidden;
    bottom: 0;
    left: 0;
    right: 0;
    top: 50px;
}
#pagesort #pagesort-page_list_wrapper ul {
      position: relative;
      padding-bottom: 20px;
}
#pagesort #pagesort-page_list_wrapper ul li {
        position: absolute;
        cursor: move;
        cursor: -webkit-grab;
        margin: 20px 20px 0 20px;
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
}
#pagesort #pagesort-page_list_wrapper ul li .list_page-item-preview {
          width: 70px;
          height: 35px;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          background: white;
          -webkit-box-shadow: 0 1px 4px #999;
          box-shadow: 0 1px 4px #999;
          margin: 0 auto 3px;
          position: relative;
}
#pagesort #pagesort-page_list_wrapper ul li .list_page-item-preview .page-thumb-container {
            float: left;
            height: 100%;
            width: 50%;
            position: relative;
            z-index: 1;
            border: 0 solid transparent;
            pointer-events: none;
}
#pagesort #pagesort-page_list_wrapper ul li .list_page-item-preview.towpage:after {
            content: '';
            width: 1px;
            background: #ccc;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            -webkit-box-shadow: 0 0 1px #ccc;
            box-shadow: 0 0 1px #ccc;
            z-index: 1;
}
#pagesort #pagesort-page_list_wrapper ul li.disable {
          opacity: .6;
          pointer-events: none;
}
#pagesort #pagesort-page_list_wrapper ul li:active {
          cursor: -webkit-grabbing;
          opacity: 0.8;
          z-index: 1000;
}
#pagesort #pagesort-page_list_wrapper ul li:after {
          content: attr(txt);
          display: block;
          text-align: center;
          line-height: 17px;
          width: 100%;
          color: #8c8c8c;
}
.focus-item {
  position: absolute;
  margin-left: 20px;
}
.focus-item .focus-container {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background: #fff;
    -webkit-box-shadow: 0 1px 4px #999;
    box-shadow: 0 1px 4px #999;
    margin: 0 auto 3px;
    position: relative;
    opacity: .6;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}
.focus-item .focus-container .page-thumb-container {
      float: left;
}
.focus-item:after {
    content: attr(txt);
    text-align: center;
    width: 100%;
    opacity: .2;
    display: block;
}
.focus-item:before {
    content: ' ';
    position: absolute;
    width: 100%;
    height: 70px;
    top: 0;
    left: 0;
    z-index: 2;
}
.insertLine {
  position: absolute;
  width: 4px;
  height: 110px;
  left: 500px;
  background: #489fd9;
}



